import { request } from 'utils';

const prefix = '/api/categories';

export const getGradePartTrainType = async () =>
  request({
    prefix,
    url: '/GradePart_TrainType',
    method: 'GET',
    params: {},
    data: {},
  });

export const createGradePartTrainType = async values =>
  request({
    prefix,
    url: '/GradePart_TrainType',
    method: 'POST',
    params: {},
    data: { ...values },
  });

export const deleteGradePartTrainType = async (gradePartId, trainTypeId) =>
  request({
    prefix,
    url: `/GradePart_TrainType/${gradePartId}/traintype/${trainTypeId}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



