import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getGradePart } from 'services/gradePart';
import { createGradePartTrainType } from 'services/gradePartTrainType';

@Form.create({ name: 'FormGradePartTrainType' })
class FormGradePartTrainType extends Component {
  state = {
    initialValue: {},
  };

  saveGradePartTrainType = async values => {
    await createGradePartTrainType(values);
    message.success('Lưu thành công');
    this.props.history.goBack();
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.saveGradePartTrainType(values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} tòa nhà`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Mã thành phần điểm" required>
              {getFieldDecorator('gradePartId', {
                rules: [requiredRule],
                initialValue: initialValue.gradePartId,
              })(<SelectAjax service={getGradePart} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Hệ đào tạo" required>
              {getFieldDecorator('trainTypeId', {
                rules: [requiredRule],
                initialValue: initialValue.trainTypeId,
              })(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGradePartTrainType {...props} />
  </Page>
));



